/* Carousel de tecnologías */
.technologies-carousel {
  width: 100vw;
  max-width: 1200px;
  margin: 40px auto 0 auto;
  overflow: hidden;
  position: relative;
  background: none;
  border-radius: 0;
  box-shadow: none;
  height: 90px;
}

.carousel-track {
  display: flex;
  align-items: center;
  gap: 2.5rem;
  width: max-content;
  animation: scroll-carousel 18s linear infinite;
}

@keyframes scroll-carousel {
  0% { transform: translateX(0); }
  100% { transform: translateX(-50%); }
}

.technology-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 80px;
  transition: transform 0.2s;
}
.technology-item:hover {
  transform: scale(1.12);
}
.technology-icon {
  width: 48px;
  height: 48px;
  margin-bottom: 8px;
  object-fit: contain;
}
.technology-label {
  font-size: 0.95rem;
  color: #3d3427;
  font-weight: 500;
  text-align: center;
}
@media (max-width: 600px) {
  .technologies-carousel {
    gap: 1.2rem;
    padding: 10px 0 10px 0;
  }
  .technology-icon {
    width: 36px;
    height: 36px;
  }
  .technology-label {
    font-size: 0.8rem;
  }
}
