/* Academia Page Styles */
.academia-section {
  min-height: 100vh;
  background: linear-gradient(135deg, #f5f1eb 0%, #e8ddd4 100%);
  padding: 2rem 0;
  overflow-x: hidden;
}

.academia-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
  display: flex;
  flex-direction: row;
  gap: 2rem;
  min-height: 80vh;
  align-items: flex-start;
}

/* Header Section */
.academia-header {
  flex: 0 0 220px;
  position: sticky;
  top: 2rem;
}

.academia-header h2 {
  font-size: 3rem;
  color: #8b7355;
  margin-bottom: 0.5rem;
  font-weight: 300;
  letter-spacing: -0.02em;
}

.academia-subtitle {
  font-size: 1.2rem;
  color: #a68b5b;
  font-weight: 300;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}

/* Timeline Section */
.academia-timeline {
  flex: 2 1 0;
  position: relative;
  padding-left: 2rem;
  min-width: 0;
}

.academia-timeline::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 2px;
  background: linear-gradient(to bottom, #8b7355, #a68b5b);
}

.timeline-item {
  position: relative;
  margin-bottom: 3rem;
  padding-left: 2rem;
}

.timeline-item::before {
  content: '';
  position: absolute;
  left: -1rem;
  top: 0.5rem;
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  background: #8b7355;
  border: 3px solid #f5f1eb;
  box-shadow: 0 0 0 3px #8b7355;
}

.timeline-year {
  font-size: 0.9rem;
  color: #a68b5b;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  margin-bottom: 0.5rem;
}

.timeline-content h3 {
  font-size: 1.5rem;
  color: #5a4a3a;
  margin-bottom: 0.5rem;
  font-weight: 500;
}

.institution {
  font-size: 1.1rem;
  color: #8b7355;
  font-weight: 500;
  margin-bottom: 0.75rem;
}

.description {
  font-size: 1rem;
  color: #6b5b4a;
  line-height: 1.6;
  margin-bottom: 0;
}

/* Highlights Section */
.academia-highlights {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
  margin-top: 0;
  padding-top: 0.5rem;
  border-top: none;
  flex: 1 1 0;
  min-width: 0;
}

.highlight-section h4 {
  font-size: 1.05rem;
  color: #8b7355;
  margin-bottom: 0.5rem;
  font-weight: 500;
}

.highlight-section.publicaciones {
  margin-left: 0;
  width: 100%;
  max-width: 700px;
  min-width: 350px;
  flex: 1 1 0;
}


.highlight-section ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.highlight-section li {
  padding: 0.3rem 0;
  border-bottom: 1px solid #e8ddd4;
  color: #6b5b4a;
  font-size: 0.85rem;
  line-height: 1.4;
}

.highlight-section li:last-child {
  border-bottom: none;
}

/* Responsive Design */
@media (max-width: 768px) {
  .academia-content {
    flex-direction: column;
    gap: 2rem;
    padding: 1rem;
  }

  .academia-header {
    flex: none;
    position: static;
  }

  .academia-header h2 {
    font-size: 2.5rem;
  }

  .academia-timeline {
    padding-left: 1.5rem;
  }

  .timeline-item {
    padding-left: 1.5rem;
  }

  .academia-highlights {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 480px) {
  .academia-header h2 {
    font-size: 2rem;
  }

  .academia-subtitle {
    font-size: 1rem;
  }

  .timeline-content h3 {
    font-size: 1.3rem;
  }
}

/* Animation for horizontal scroll */
.academia-content {
  transform: translateX(0);
  transition: transform 0.3s ease-out;
}

/* Hover effects */
.timeline-item {
  transition: all 0.3s ease;
}

.timeline-item:hover {
  transform: translateX(5px);
}

.timeline-item:hover::before {
  background: #a68b5b;
  box-shadow: 0 0 0 3px #a68b5b;
  transform: scale(1.2);
}