
.projects-page-bg {
  min-height: 100vh;
  width: 100vw;
  background: linear-gradient(135deg, #f5f1eb 0%, #e8ddd4 50%, #d4c4b0 100%);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 60px 0 40px 0;
}

.projects-title {
  font-size: 2.8rem;
  font-weight: 700;
  color: #3d3427;
  margin-bottom: 2.5rem;
  letter-spacing: 0.08em;
  text-align: center;
  font-family: 'Helvetica Neue', Arial, sans-serif;
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 2.5rem;
  width: 90vw;
  max-width: 1200px;
}

.project-card {
  background: #fff8f6;
  border-radius: 22px;
  box-shadow: 0 6px 32px rgba(61,52,39,0.10);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  transition: transform 0.2s, box-shadow 0.2s;
  border: 1px solid #e8ddd4;
}

.project-card:hover {
  transform: translateY(-8px) scale(1.03);
  box-shadow: 0 12px 40px rgba(61,52,39,0.18);
}

.project-image {
  width: 100%;
  height: 180px;
  object-fit: cover;
  border-top-left-radius: 22px;
  border-top-right-radius: 22px;
}

.project-content {
  padding: 1.5rem 1.2rem 1.2rem 1.2rem;
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.project-title {
  font-size: 1.3rem;
  font-weight: 600;
  color: #3d3427;
  margin-bottom: 0.7rem;
  font-family: 'Helvetica Neue', Arial, sans-serif;
}

.project-description {
  font-size: 1rem;
  color: #6d5c43;
  margin-bottom: 1.2rem;
  flex: 1;
}

.learn-more {
  display: inline-block;
  background: #3d3427;
  color: #fff8f6;
  padding: 0.6rem 1.3rem;
  border-radius: 18px;
  font-size: 1rem;
  font-weight: 500;
  text-decoration: none;
  transition: background 0.2s, color 0.2s;
  box-shadow: 0 2px 8px rgba(61,52,39,0.08);
  margin-top: 0.5rem;
  text-align: center;
}

.learn-more:hover {
  background: #a68b5b;
  color: #3d3427;
}

@media (max-width: 900px) {
  .projects-grid {
    grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
    gap: 1.5rem;
  }
  .project-image {
    height: 140px;
  }
}

@media (max-width: 600px) {
  .projects-title {
    font-size: 2rem;
    margin-bottom: 1.2rem;
  }
  .projects-page-bg {
    padding: 30px 0 20px 0;
  }
}
